:root {
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram-start:hsl(37, 97%, 70%);
  --Instagram-end:hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --toggle-bg-start:hsl(210, 78%, 56%);
  --toggle-bg-end:hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg:hsl(230, 17%, 14%);
  --dark-top-bg:hsl(232, 19%, 15%);
  --dark-card:hsl(228, 28%, 20%);
  --dark-text1:hsl(228, 34%, 66%);
  --dark-text2:hsl(0, 0%, 100%);
  --light-bg:hsl(0, 0%, 100%);
  --light-top-bg:hsl(225, 100%, 98%);
  --light-card:hsl(227, 47%, 96%);
  --light-text1:hsl(228, 12%, 44%);
  --light-text2:hsl(230, 17%, 14%);
}

:root {
  --font-inter:"Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text2);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}

@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}
.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
  }
}
.header__subtitle {
  font-size: 1.0769230769rem;
  font-weight: 700;
  color: var(--dark-text1);
}
.toggle {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  border: none;
}
.toggle label[for=dark] {
  grid-column: 1/2;
}
.toggle label[for=system] {
  grid-column: 1/end;
  grid-row: 2/3;
  justify-self: center;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0;
  opacity: 0;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: linear-gradient(255deg, var(--toggle-bg-start) 0%, var(--toggle-bg-end) 98.82%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.125rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 150ms ease-in-out;
}
.toggle__button ~ #light:checked {
  right: 0;
  left: 100%;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.card {
  padding: 1.25rem;
}